.edit-profile .light-blue-bg{
    padding: 30px;
}

.form-block-title{
    font-size: 18px;
    font-weight: 600;
    color: #080036;
    padding-bottom: 30px;
    padding-top: 20px;
}

.upload_certificate{
    position: relative;
    cursor: pointer;
}
.upload_certificate input[type="file"]{
    position: absolute;
    left: -9999px;
    top: -9999px;
}
.upload_certificate::before{
    content: url('../../images/icons/ic-attachment.svg');
    position: absolute;
    right: 15px;
    top: 14px;
}

.user-profile-img-wrap{
    display: inline-block;
    position: relative;
    margin-bottom: 50px;
}

.user-profile-img{
    width: 118px;
    height: 118px;
    overflow: hidden;
    border-radius: 50%;
    border: 2px solid #2F80ED;
}

.upload-profileImg-btn{
    width: 36px;
    height: 36px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: #2F80ED;
    border: 1px solid #FAFFFE;
    vertical-align: middle;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    right: 0;
    bottom: 0;
    cursor: pointer;
}

.upload-profileImg-btn input[type="file"]{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    opacity: 0;
    cursor: pointer;
}